import headers from "../../components/header_nav.vue";
import service from "../../service.js";
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		headers,
		uniLoadMore
	},
	data() {
		return {
			dataList:[],
		
			textid: '',//文章id
			url_left:"/pages/main/index",
			
			navid:1,
		
			textlist:[
				// {
				// 	id:1,
				// 	title:"捐资助学文章标题",
				// 	text:"捐资助学文章标题，捐资助学文章标题，捐资助学文章标题",
				// 	time:"2020-03-23"
				// },	
			],
				loadshow: true,
				colorshow: false,
				isLoading: false,
				scroll: false,
				status: 0,
				pages: 1,
				limit: 15,
				page: 0,
				out: 0,
		}
	},
	methods:{
		jump(){
				uni.navigateBack({
					delta:1
				})
		},
		jum(id,type){
			uni.navigateTo({
				url:'/pages/index/article?id='+id+'&&type='+type 
			})
		},
	carDetails(url){
		uni.navigateTo({
			url: url
		});
	},
		lower(){
			// console.log("121")
		},
		Loadthepage() {
			
			var self = this;
			if (!self.loadshow) {
				return;
			}
			if (self.page <= self.pages) {
				var page = self.page + 1;
				this.$service.auth(
					this,
					this.$service.api.index.cargetArticle,
					'GET',
					{
						page: page,
						limit: 15,
					},
					function(self, res) {
						console.log(res);
						self.pages = res.data.last_page;
						self.page = page;
						// self.activity = res.data.activity
						var list = res.data.data;
						self.usdt = list.usdt;
						self.alipay = list.alipay;
						list.forEach((v, i) => {
							self.dataList.push(v);
						});
						self.$forceUpdate();
						console.log(self.dataList);
						if (self.pages == self.page || self.dataList.length == 0) {
							self.loadshow = false;
							uni.showToast({
								title: '数据已加载完毕',
								icon: 'none'
							});
							console.log('1', self.page, self.pages);
						} else {
							self.loadshow = true;
							console.log('2', self.page, self.pages);
						}
					}
				);
			} else {
				self.loadshow = false;
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
				console.log('3', self.page, self.pages);
			}
		},
		
	},
	onLoad() {
	this.Loadthepage()
		// service.auth(this , service.api.index.cararticleDetail , 'GET' ,  function(self , res){
		// 	// console.log(res);
		// 	// self.textlist = res.data.list;
		// });
		// this.loadData();
	}
	
}
